import React from 'react';
import PropTypes from 'prop-types';
import marked from 'marked';
import OptionsIntro from '../OptionsIntro';
import './index.less';

const Sidebar = (props) => {
  let navigation = null;
  let sidebarContent = null;
  // let sidebarPosition = null;
  if(props.sidebar) {
    sidebarContent = (
      <div className="sidebar_content" dangerouslySetInnerHTML={ {"__html": marked(props.sidebar.content)} } />
    )
    // sidebarPosition = props.sidebar.position;
  }
  if(props.navigation) {
    navigation = (
      <div className="navigation">
        <OptionsIntro
        headlineText={props.navigation.title}
        goBack={props.goBack}
        goBackText={props.navigation.linkText}
        />
      </div>
    );
  }
  return (
    <div className="sidebar_section">
      { navigation }
      { sidebarContent }
    </div>
  );
};

export default Sidebar;

Sidebar.propTypes = {
  goBack: PropTypes.func
};

Sidebar.displayName = 'Sidebar';



